/* eslint-disable no-undef */
import Vue from 'vue';
import Router from 'vue-router';
import Main from '@/components/public/Main';

Vue.use(Router);

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    // Public
    {
      path: '/',
      name: 'Main',
      component: Main,
      meta: {
        isPublic: true
      }
    }
  ]
});
