<template>
  <div :class="simpleClass">
    <div class="Card__Subtitle">
      {{ subtitle }}
    </div>
    <div
      class="Text__Medium Card__Title"
      :class="simpleTitleClass"
      :style="fontStyle"
    >
      {{ title }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'CustomTitle',
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    },
    isSimple: {
      type: Boolean,
      default: false
    },
    titleClass: String,
    fontSize: {
      type: String,
      default: null
    }
  },
  computed: {
    simpleClass() {
      return this.isSimple || this.fontSize
        ? 'Margin__Bottom--0'
        : 'Margin__Bottom--48';
    },
    simpleTitleClass() {
      return this.titleClass;
    },
    fontStyle() {
      return this.fontSize ? `font-size: ${this.fontSize}px; margin:0` : '';
    }
  }
};
</script>

