<template lang="pug">
  <div>
    
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'loading',

  methods: {
    showLoading(title) {
      this.$swal.fire({
        title,
        allowOutsideClick: false,
        customClass: {
          popup: 'Loading'
        },
        onBeforeOpen: () => {
          this.$swal.showLoading();
        }
      });
    }
  },

  computed: {
    ...mapState(['isLoading'])
  },

  watch: {
    isLoading(title) {
      title ? this.showLoading(title) : this.$swal.close();
    }
  }
};
</script>

<style lang="stylus">
  .swal2-popup.Loading
    background rgba(0,0,0,0.8)
    border-radius 15px
    max-width 76%

    .swal2-title
      color #CCC
</style>
