<template>
  <div class="Card Card__Car">
    <div class="Flex Flex__SpaceBetween Flex__Middle Car__Divider">
      <div>
        <div class="Car__Label">
          {{ ops[0] }}
        </div>
        <div class="Car__Text">
          {{ values[0] }}
        </div>
      </div>

      <div v-if="workshop" :class="isOn()"></div>
    </div>

    <div class="Car__Label">
      {{ ops[1] }}
    </div>
    <div class="Car__Text Car__Divider">
      {{ values[1] }}
    </div>

    <div class="Flex Flex__SpaceBetween Flex__Middle">
      <div>
        <div class="Car__Label">
          {{ ops[2] }}
        </div>
        <div class="Car__Body">
          {{ values[2] }}
        </div>
      </div>
      <div class="Divider--Y"></div>
      <div style="margin-left:-10px">
        <div class="Car__Label">
          {{ ops[3] }}
        </div>
        <div class="Car__Body">
          {{ values[3] }}
        </div>
      </div>
    </div>

    <custom-button
      v-if="!workshop"
      text="Ver detalhes"
      color="White"
      class="Margin__Top--36 Button__Full Button__Car"
      iconFront="true"
      @click="$emit('details')"
    ></custom-button>
  </div>
</template>

<script>
import { Button } from '../form/Index';

export default {
  name: 'CardScrollComponent',
  components: {
    'custom-button': Button
  },
  props: {
    opsCar: {
      type: Array,
      default: () => ['prÃ³xima revisÃ£o', 'modelo', 'placa', 'kilometragem']
    },
    opsBudget: {
      type: Array,
      default: () => ['OFICINA', 'CARRO PARA A AVALIAÃÃO', 'STATUS', 'VALOR']
    },
    opsWorkshop: {
      type: Array,
      default: () => [
        'oficina',
        'localizaÃ§Ã£o',
        'distÃ¢ncia',
        'tempo de resposta'
      ]
    },
    car: {
      type: Object,
      default: null
    },
    budget: {
      type: Object,
      default: null
    },
    workshop: {
      type: Object,
      default: null
    },
    isSelected: {
      type: Boolean,
      default: () => false
    }
  },
  methods: {
    getCarInArray() {
      if (this.car) {
        return [
          'Aguardando avaliaÃ§Ã£o',
          'Aguardando avaliaÃ§Ã£o',
          this.car.plate,
          this.car.mileage
        ];
      }
    },
    getbudgetInArray() {
      if (this.budget) {
        return [
          this.budget.name,
          this.budget.carModel,
          this.budget.status,
          this.budget.value
        ];
      }
    },
    getWorkshopInArray() {
      if (this.workshop) {
        return [
          this.workshop.name,
          this.workshop.location,
          this.workshop.distance,
          this.workshop.responseTime
        ];
      }
    },
    isOn() {
      return `Card__ItemSelected--${this.isSelected ? 'on' : 'off'}`;
    }
  },
  computed: {
    ops() {
      if (this.car) return this.opsCar;
      if (this.budget) return this.opsBudget;
      if (this.workshop) return this.opsWorkshop;
      return null;
    },
    values() {
      if (this.car) return this.getCarInArray();
      if (this.budget) return this.getbudgetInArray();
      if (this.workshop) return this.getWorkshopInArray();
      return null;
    }
  }
};
</script>

