<template>
  <button :class="classColor" @click="$emit('click', true)">
    <div :class="menuButton" v-if="icon" :icon="icon"/>
    <span v-if="!icon" class="Text__Action">{{ text }}</span>
    <div v-if="iconFront"></div>
    <!-- Image  -->
  </button>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: ''
    },

    color: {
      type: String,
      default: ''
    },

    icon: {
      type: String
    },

    type: {
      type: String,
      default: 'Normal'
    },
    iconFront: {
      type: String
    }
  },

  computed: {
    classColor() {
      return `Button Button__${this.color} Button__${this.type}`;
    },
    menuButton() {
      return 'Button__Icon'
    }
  }
};
</script>

<style lang="stylus">
@import '~@/assets/styles/button.styl'
</style>
