<template>
  <div class="Field">
    <div :class="classLabelColor" class="Text__Size--16 Margin__Bottom--12">
      {{ label }}
    </div>

    <datetime
      type="date"
      v-model="value"
      v-if="typeField === 'date'"
      input-class="Field__Datetime"
      :placeholder="placeholder"
      @input="handleDateInput"
      value-zone="UTC-3"
      max-datetime="maxDate"
      :disabled="isReadOnly"
    ></datetime>

    <datetime
      type="datetime"
      v-else-if="typeField === 'datetime'"
      input-class="Field__Datetime"
      :placeholder="placeholder"
      @input="handleDateInput"
      value-zone="UTC-3"
      max-datetime="maxDate"
      :readonly="isReadOnly"
    ></datetime>

    <textarea
      :type="typeField"
      :placeholder="placeholder"
      :class="classBorderColor"
      :maxlength="maxLength"
      :value="value"
      v-else-if="typeField === 'textarea'"
      @input="handleInput"
      class="Field__Textarea"
      :readonly="isReadOnly"
    />

    <input
      @input="handleInput"
      :value="value"
      :type="typeField"
      :placeholder="placeholder"
      :class="classBorderColor"
      :maxlength="maxLength"
      v-mask="mask"
      v-else-if="mask"
      class="Field__Input"
      :readonly="isReadOnly"
    />

    <input
      @input="handleInput"
      :value="value"
      :type="typeField"
      :placeholder="placeholder"
      :class="classBorderColor"
      :maxlength="maxLength"
      v-else
      class="Field__Input"
      :readonly="isReadOnly"
    />
  </div>
</template>

<script>
import { mask } from 'vue-the-mask';

export default {
  name: 'Field',

  directives: {
    mask
  },

  data() {
    return {
      trap: ''
    };
  },

  props: {
    value: {},

    label: {
      type: String
    },

    valueField: {
      type: String,
      default: ''
    },

    typeField: {
      type: String,
      default: 'text'
    },

    options: {
      type: Array
    },

    placeholder: {
      type: String
    },

    labelColor: {
      type: String,
      default: 'White'
    },

    borderColor: {
      type: String,
      default: 'White'
    },

    pattern: {
      type: String
    },

    maxLength: {
      type: Number
    },

    mask: {
      type: String
    },

    isReadOnly: {
      type: Boolean,
      default: false
    },

    traps: {
      type: String
    },

    maxDate: {
      type: Date,
      default() {
        return new Date();
      }
    }
  },

  computed: {
    classLabelColor() {
      return `Text Text__${this.labelColor}`;
    },

    classBorderColor() {
      return `Field__Border--${this.borderColor}`;
    }
  },

  methods: {
    handleInput($event) {
      this.$emit('input', $event.target.value);
    },
    handleDateInput(value) {
      this.$emit('input', value);
    }
  },

  mounted() {
    if (this.traps) {
      let copy = Object.assign(this.traps, {});
      this.trap = copy;
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/field.styl'

.scroller
    height 100%
</style>
