<template>
  <div class="Field">
    <input
      type="checkbox"
      @change="handleInput"
      :checked="value"
      :class="classLabelColor"
      :id="nameSelect"
      class="Text__Size--20 Field__Check"
    />
    <label :for="nameSelect" :class="classLabelColor">{{ label }}</label>
  </div>
</template>

<script>
import uuidv1 from 'uuid/v1';

export default {
  name: 'Check',

  data() {
    return {
      nameSelect: uuidv1()
    };
  },

  props: {
    value: {
      type: Boolean
    },

    label: {
      type: String
    },

    labelColor: {
      type: String,
      default: 'White'
    },

    borderColor: {
      type: String,
      default: 'White'
    }
  },

  computed: {
    classLabelColor() {
      return `Text Text__${this.labelColor}`;
    },

    classBorderColor() {
      return `Field__Border--${this.borderColor}`;
    }
  },

  methods: {
    handleInput($event) {
      this.$emit('input', $event.target.checked);
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/field.styl'
</style>
