<template>
  <div class="Field Field__Combo">
    <div :class="classLabelColor" class="Text__Size--16 Margin__Bottom--12">
      {{ label }}
    </div>

    <select
      @input="$emit('input', $event.target.value)"
      :class="classBorderColor"
      :placeholder="placeholder"
      :disabled="isReadOnly"
      class="Field__Select Flex Flex__Middle"
    >
      <option value="" :selected="value == ''" disabled
        >Selecione um item</option
      >
      <option
        :selected="text == value"
        :value="valueToSelect"
        :key="index"
        v-for="({ value: valueToSelect, text }, index) in options"
      >
        {{ text }}
      </option>
    </select>
  </div>
</template>

<script>
export default {
  name: 'Field',

  data() {
    return {
      content: ''
    };
  },

  props: {
    value: {
      type: String
    },

    label: {
      type: String
    },

    options: {
      type: Array
    },

    placeholder: {
      type: String
    },

    labelColor: {
      type: String,
      default: 'White'
    },

    borderColor: {
      type: String,
      default: 'White'
    },

    mask: {
      type: String
    },

    isReadOnly: {
      type: Boolean,
      default: null
    }
  },

  computed: {
    classLabelColor() {
      return `Text Text__${this.labelColor}`;
    },

    classBorderColor() {
      return `Field__Border--${this.borderColor}`;
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/field.styl'

.scroller
  height 100%
</style>
