<template>
  <div>
    <p>{{ label }}</p>
    <autocomplete
      :aria-label="placeholder"
      :placeholder="placeholder"
      :search="search"
      :get-result-value="getResultValue"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import Autocomplete from '@trevoreyre/autocomplete-vue';
import '@trevoreyre/autocomplete-vue/dist/style.css';
export default {
  name: 'CustomSearch',
  components: {
    autocomplete: Autocomplete
  },
  props: {
    label: {
      type: String,
      default: () => 'O que vocÃª estÃ¡ precisando hoje?'
    },
    placeholder: {
      type: String,
      default: () => 'OrÃ§amentos'
    },
    options: {
      type: Array,
      default: () => ['OrÃ§amentos', 'Agendamentos', 'Reparos', 'Garantias']
    }
  },
  methods: {
    search(input) {
      if (input.length < 1) {
        return [];
      }
      return this.options.filter(opt => {
        return opt.toLowerCase().startsWith(input.toLowerCase());
      });
    },
    getResultValue(result) {
      return result;
    },
    handleSubmit(result) {
      this.$emit('input', result);
    }
  }
};
</script>

